import { Sky } from "@react-three/drei";
import { DirectionalLight } from "three";

export default function Env() {
  return (
    <>
      {/* Ciel avec effets de dgrad et lumire */}
      <Sky
        distance={450000} // Distance du ciel
        sunPosition={[1, 1, 0]} // Position du soleil pour dfinir les ombres et la lumire
        inclination={1} // Inclinaison du soleil
        azimuth={0.25} // Azimut du soleil (contrle la direction de la lumire)
        turbidity={10} // Turbidit pour ajuster l'effet de brouillard
        rayleigh={1} // Augmenter l'azurit pour un ciel plus lumineux et plus vibrant
        mieCoefficient={2} // Effet de lumire diffuse
        mieDirectionalG={0.8} // Eclairement diffus
        exposure={5} // Augmenter lgrement l'exposition pour plus de saturation
      />
      {/* Lumire directionnelle pour clairer la scne */}
      <directionalLight
        position={[5, 5, 5]} // Position de la lumire
        intensity={1.2} // Augmenter l'intensit de la lumire pour plus de contraste et de couleur
        castShadow={true} // Activer les ombres
      />
      {/* Lumire ambiante pour adoucir l'clairage global */}
      <ambientLight intensity={0.9} />
      {/* Rduire l'intensit pour plus de contraste et de saturation */}
    </>
  );
}
